@my-bgcolor: #e48632;
@my-bgcolor-d1: #d67f2d;
@my-text-color: #6f5a40;
@my-body-bgcolor: #f5f6f6;

body {
  color: @my-text-color;
  background: @my-body-bgcolor;
}

a {
  color: #08c;
  text-decoration: none;
  &:hover {
    color: #e48632;
    text-decoration: underline;
  }
}

p {
  margin: 0 0 10px;
}

img {
  max-width: 100%;
  width: auto\9;
  height: auto;
  vertical-align: middle;
  border: 0;
}
pre {
  display: block;
  padding: 9.5px;
  margin: 0 0 10px;
  font-size: 13px;
  line-height: 20px;
  word-break: break-all;
  word-wrap: break-word;
  white-space: pre;
  white-space: pre-wrap;
  background-color: #f5f5f5;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}

code,
pre {
  font-size: 13px;
  font-family: Monaco, Consolas, "Lucida Console", monospace;
}
code {
  padding: 2px 4px;
  color: #d14;
  background-color: #f7f7f9;
  border: 1px solid #e1e1e8;
  white-space: nowrap;
  border-radius: 3px;
}
pre code {
  font-size: 14px;
  padding: 1em;
  color: #f8f8f8;
  background-color: #181818;
  display: block;
}

.navbar-inverse .navbar-inner {
  padding: 0;
  border: 0;
  border-radius: 0;
  filter: none;
  background-image: none;
  background-color: @my-bgcolor;
}

.navbar .container {
  margin-left: auto;
  margin-right: auto;
}

.navbar .nav {
  margin: 0 10px 0 155px;
  font-size: 17px;
  font-weight: 400;
  letter-spacing: 1px;
}

.nav-collapse {
  position: relative;
}

.navbar .nav > li > a {
  display: block;
  padding: 18px 15px 18px;
  color: #fff;
  text-shadow: 1px 1px 2px rgb(0 0 0 / 25%);

  &:hover {
    background-color: @my-bgcolor-d1;
    color: #fff;
    text-decoration: none;
  }

  i {
    margin-right: 5px;
    display: inline-block;
    vertical-align: middle;
    margin-top: 1px;
  }
}

.page-index .brand {
  position: absolute;
  top: 5px;
}
.page-index .brand .logo {
  position: relative;
  z-index: 3;
}
.brand .logo {
  background-image: url(img/igcompsci-logo.svg);
  display: block;
  width: 150px;
  height: 90px;
}

.main {
  min-height: 600px;
  background-color: #f5f6f6;

  .main-content {
    min-height: 600px;
    padding: 36px 40px 36px 40px;
    background-color: #fff;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
  }
}

.main-content {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 10px 0;
    font-family: inherit;
    font-weight: 700;
    line-height: 20px;
    color: inherit;
    text-rendering: optimizelegibility;
  }

  h1,
  h2,
  h3 {
    line-height: 40px;
  }

  h1 {
    font-family: sans-serif;
    font-size: 32px;
    color: #453015;
    margin: 50px 0 0.8em;
  }
  h2 {
    font-size: 24px;
    margin-top: 2em;
  }
  h3 {
    font-size: 1.2em;
    margin-top: 1em;
  }
}

.footer {
  font-size: 14px;
  color: #fff;
  height: 4em;
  line-height: 4em;
  letter-spacing: 1px;
  text-shadow: 1px 1px 2px rgb(0 0 0 / 25%);

  .inner {
    background-color: #453015;
  }
}

.footer-main {
  li a {
    color: #fff;
    padding: 1.4em 1em;

    &:hover {
      text-decoration: none;
      background-color: #3e2b13;
    }
  }

  li.social a {
    font-size: 1.3em;
    padding: 0.95em 0.6em 0.73em;
    vertical-align: middle;
  }
}

.main-content .title-header {
  font-family: sans-serif;
  font-size: 32px;
  color: #453015;
  margin: 0 0 0.8em;

  .name {
    font-size: 2.8em;
    font-weight: 900;
    letter-spacing: 0.06em;
    -webkit-text-stroke: 4px;
  }

  .tag-line {
    display: block;
    margin-top: -0.3em;
    font-weight: 700;
    font-size: 1.6em;
    margin-bottom: 10px;
    letter-spacing: -1.5px;
    text-transform: uppercase;
  }
}
